.wrapper {
  display: -ms-flexbox;
  display: flex;
  padding: 15px 50px 15px 0;
  border-top: 1px dashed #dddddd; }

.imageWrapper {
  display: block;
  width: 100px;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  height: 140px;
  margin: 0 20px 0 0;
  overflow: hidden; }

.loading {
  display: block;
  margin: 40px auto 0 auto; }

.imageWrapper img {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%; }

.title {
  color: #3576aa;
  font-size: 14px; }

.infos {
  font-size: 14px;
  color: #666;
  margin-top: 5px; }

:global(.rater .ant-rate-star) {
  font-size: 12px;
  margin-right: 2px; }

.average {
  color: #e2952b; }



