.content {
  width: 1040px;
  margin: 20px auto 0 auto;
  color: #111111; }

.main {
  width: 675px; }

.title {
  color: #494949;
  font-size: 30px; }
  .title span {
    color: #888888; }

.introducePanel {
  margin-top: 10px;
  display: -ms-flexbox;
  display: flex; }

.sectionTitle {
  color: #087626;
  font-size: 15px;
  margin: 20px 0 15px 0; }

/* MainPic */
.mainPicWrapper {
  width: 130px;
  margin: 0 20px 0 0; }
  .mainPicWrapper img {
    width: 100%; }

/* Info */
.infoPanel {
  font-size: 13px;
  max-width: 333px;
  margin-right: 20px;
  line-height: 20px; }

.infoQuery {
  color: #686868; }

.infoLink {
  color: #357bb3; }
  .infoLink:hover {
    color: #fff;
    background-color: #357bb3; }

/* RatePanel */
.rateWrapper {
  border-left: 1px solid #eaeaea;
  padding: 0 0 0 20px; }

.rateTitle {
  color: #a5a5a5; }

.starWrapper {
  display: -ms-flexbox;
  display: flex; }

.average {
  font-size: 24px;
  font-weight: bolder;
  color: #494949;
  margin: 0 10px 0 0; }

.starNum {
  color: #a5a5a5;
  margin: 0 5px 0 0; }

.rateNum {
  white-space: nowrap; }

.power {
  height: 10px;
  display: inline-block;
  background-color: #fed69a;
  margin: 0 10px 0 0; }

/* Summary */
/* Celebrities */
.celebrity {
  display: inline-block;
  margin-right: 20px; }
  .celebrity img {
    width: 100px; }

.name {
  text-align: center; }
  .name a {
    color: #357bb3; }
  .name:hover a {
    color: #fff;
    background-color: #357bb3; }

.indentity {
  text-align: center;
  color: #9c9c9c; }

/* RelatedPic */
.picNum {
  font-size: 13px;
  font-weight: normal;
  color: #686868; }
  .picNum a {
    color: #357bb3; }

.picUl li {
  display: inline-block;
  margin-right: 2px; }
  .picUl li img {
    height: 115px; }

.photo {
  -o-object-fit: cover;
     object-fit: cover;
  width: 115px; }

/* PopularComments */
h2 * {
  font-size: 13px;
  font-weight: normal; }

h2 a {
  color: #357bb3; }

h2 span {
  color: #686868; }

.noRate {
  color: #999; }



